<template>
	<div class="file-container"
		:class="{selected: selected}">
		<CheckboxRadioSwitch class="selection-checkbox" :checked="selected" @update:checked="onToggle" />

		<a class="file"
			:href="davPath"
			:aria-label="ariaLabel"
			@click.prevent="openViewer">

			<div v-if="item.injected.mime.includes('video') && item.injected.hasPreview" class="icon-video-white" />

			<!-- image and loading placeholder -->
			<transition-group name="fade" class="transition-group">
				<img v-if="!error"
					ref="img"
					:key="`${item.injected.basename}-img`"
					:src="src"
					:alt="item.injected.basename"
					:aria-describedby="ariaUuid"
					@load="onLoad"
					@error="onError">

				<div v-if="!loaded || error"
					:key="`${item.injected.basename}-svg`"
					class="loading-overlay">
					<svg xmlns="http://www.w3.org/2000/svg"
						viewBox="0 0 32 32"
						fill="url(#placeholder__gradient)">
						<use v-if="isImage" href="#placeholder--img" />
						<use v-else href="#placeholder--video" />
					</svg>
				</div>
			</transition-group>

			<!-- image name and cover -->
			<p :id="ariaUuid" class="hidden-visually">{{ item.injected.basename }}</p>
		</a>
	</div>
</template>

<script>
import { generateRemoteUrl, generateUrl } from '@nextcloud/router'
import { getCurrentUser } from '@nextcloud/auth'
import { CheckboxRadioSwitch } from '@nextcloud/vue'

import UserConfig from '../mixins/UserConfig'

export default {
	name: 'File',
	components: {
		CheckboxRadioSwitch,
	},
	mixins: [UserConfig],
	inheritAttrs: false,
	props: {
		item: {
			type: Object,
			required: true,
		},
		selected: {
			type: Boolean,
			required: true,
		},
	},

	data() {
		return {
			loaded: false,
			error: false,
		}
	},

	computed: {
		davPath() {
			return generateRemoteUrl(`dav/files/${getCurrentUser().uid}`) + this.item.injected.filename
		},
		ariaUuid() {
			return `image-${this.item.injected.fileid}`
		},
		ariaLabel() {
			return t('photos', 'Open the full size "{name}" image', { name: this.item.injected.basename })
		},
		isImage() {
			return this.item.injected.mime.startsWith('image')
		},
		decodedEtag() {
			return this.item.injected.etag.replace('&quot;', '').replace('&quot;', '')
		},
		src() {
			return generateUrl(`/core/preview?fileId=${this.item.injected.fileid}&c=${this.decodedEtag}&x=${512}&y=${512}&forceIcon=0&a=1`)
		},
	},

	beforeDestroy() {
		// cancel any pending load
		this.$refs.src = ''
	},

	methods: {
		openViewer() {
			OCA.Viewer.open({
				path: this.item.injected.filename,
				list: this.item.injected.list,
				loadMore: this.item.injected.loadMore ? async () => await this.item.injected.loadMore(true) : () => [],
				canLoop: this.item.injected.canLoop,
			})
		},

		/** When the image is fully loaded by browser we remove the placeholder */
		onLoad() {
			this.loaded = true
		},

		onError() {
			this.error = true
		},

		onToggle(value) {
			this.$emit('select-toggled', { fileid: this.item.injected.fileid, value })
		},
	},

}
</script>

<style lang="scss" scoped>
@import '../mixins/FileFolder';

.file-container {
	position: relative;
	border: 2px solid white; // Use border so create a separation between images.

	// Selection border.
	&.selected::after {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
		width: 100%;
		height: 100%;
		content: '';
		outline: var(--color-primary) solid 4px;
		outline-offset: -4px;
		pointer-events: none;
	}

	// Reveal checkbox on hover.
	&:hover, &.selected {
		.selection-checkbox {
			display: flex;
		}
	}

	.selection-checkbox {
		display: none;
		position: absolute;
		top: 8px;
		right: 16px;
		z-index: 1;
		width: fit-content;
	}

	a {
		width: 100%;
		height: 100%;
		box-sizing: border-box;

		.transition-group {
			display: contents;

			.icon-video-white {
				position: absolute;
				top: 10px;
				right: 10px;
				z-index: 20;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				// color: transparent; // should be diplayed on error
			}

			.loading-overlay {
				position: absolute;
				background: rgba($color: (#000000), $alpha: 0.2);
				height: 100%;
				width: 100%;
				display: flex;
				align-content: center;
				align-items: center;
				justify-content: center;

				svg {
					width: 70%;
					height: 70%;
				}
			}
		}
	}
}
</style>
